import React, {useContext} from "react"
import PropTypes from "prop-types"
import classes from "./TodoItem.module.css"
import done from "./TodoItem.css"
import Context from "./Context";

function TodoItem (props) {
    const {removeTodo} = useContext(Context)
    let styles = []
    if (props.todo.completed === true) {
        styles.push("done")
    }

    return (
        <li className={classes.Item_wrapper}>
            <span className={styles.join(" ")}>
                <input className={classes.input_item}
                       checked={props.todo.completed}
                       type="checkbox"
                       onChange={()=>props.onChange(props.todo.id)}/>
            <strong>{props.index + 1}</strong>
            &nbsp;
            {props.todo.title}
            </span>
            <button onClick={()=>{removeTodo(props.todo.id)}} className={classes.button_item}>&times;</button>
        </li>
    )
}

TodoItem.propTypes = {
    index: PropTypes.number,
    todo: PropTypes.object.isRequired
}

export default TodoItem